import React from 'react';
import Avatar from '../avatar/avatar';
export interface IQuote {
  /**
   * Adds the quote phrase
   */
  quote: string;
  /**
   * For setting the Avatar info
   */
  avatar?: {
    img?: any;
    title?: string;
    subtitle?: string;
  };
  /**
   * Additional styling if needed
   */
  className?: string;
  [others: string]: any;
}

const Quote: React.FC<IQuote> = (props) => {
  const {avatar, className, quote, ...rest} = props;

  const content = (
    <div className={`tag-ds quote-wrapper ${className || ''}`} {...rest}>
      <span className="material-icons quote-icon">format_quote</span>
      <div className="quote">{quote}</div>
      {avatar && (
        <Avatar small title={avatar.title} subtitle={avatar.subtitle} className="quote-avatar">
          {avatar.img && (typeof avatar.img === 'string' ? <img src={avatar.img} alt="quote-avatar" /> : avatar.img)}
        </Avatar>
      )}
    </div>
  );
  return content;
};

export default Quote;
